body{
    font-family: sans-serif !important;
    font-weight: 100;
}
#root1{
    background-color: green;
    color: white;
    padding: 0.5rem;
}
.logo{
    object-fit: cover;
    height: 50px;
    background-position: center;
    background-size: cover;
    color: orangered;
}
.header{
    display: flex;
    justify-content: space-around;
    border: 1px solid green;
}
.header .nav-items ul{
    list-style: none;
    display: flex;
    border: 2px solid red;
    justify-content: space-between;
    padding: 0px;
}
.header .nav-items ul li{
    padding:  0px 0.5rem;
    color: solid black;
    font-weight: 400;
    font-family: sans-serif;
}
.header .nav-items ul li:hover{
     color: orangered;
     cursor: pointer;
}

.card{
    width: 250px;
    border: 1px solid black;
    border-radius: 0.3rem ;
    padding: 0.3rem ;
    /* margin: 0.3rem; */
    margin: 0.8rem 0rem;
    font-family: sans-serif !important
}

.card > img{
    width: 100%;   
    height: 50%;
    background-size: cover;
    object-fit: center;
    background-position: center;
    border-radius: 0.3rem;
}

.resturant-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}